// Font imports 
@import url('https://fonts.googleapis.com/css?family=Reem+Kufi|Source+Sans+Pro');

$SOURCE_SANS: "Source Sans Pro", Helvetica, Arial, sans-serif;
$REEM_KUFI: "Reem Kufi", Helvetica, Arial, sans-serif;
// Some CSS Resets
html, 
body {
  font-family: $SOURCE_SANS;
  font-size: 100%;
  margin: 0;
  padding: 0;
  color: $GRAY_1;
}
// Some global styles
h1,
h2,
h3,
h4 {
  font-family: $REEM_KUFI;
}
// Todo: externalize these into different files
// Hero
.sd-hero {
  text-align: center;
  color: white;
  background-color: $PURPLE_1;

  &--title {
    font-size: 72px;
    letter-spacing: 1px;
  }
  &--subtitle {
    font-family: $SOURCE_SANS;
    font-size: 44px;
    letter-spacing: 1.67px;
  }
}
// Button styles
.sd-button {
  // Todo: semibold
  width: 216px;
  line-height: 54px;
  font-size: 24px;
  letter-spacing: 1px;
  text-transform: uppercase;
  padding: 0;
  border-radius: 10px;
  background-color: $BLUE_1;

  &.success {
    background-color: $GREEN_1;
  }
}
// Overall styles
.sd-section {
  &--header {
    font-size: 54px;
    letter-spacing: 1.5px;
    color: $BLUE_1;
  }
  &--subheader {
    font-size: 36px;
    letter-spacing: 1px;
    color: $BLUE_1;
  }
  p, 
  li {
    font-size: 28px;
    line-height: 1.4;
  }
}
// Hexagon stuff
.sd-hextion {
  background-color: $PURPLE_1;
  color: white;
}